import Vue from 'vue'
import Router from 'vue-router'

import store from '@/store'
import { CHECK_AUTH } from '@/store/actions.type'

import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

// import HelloWorld from '@/components/HelloWorld'

// import firebase from 'firebase'

// import Comics from '@/components/Comics'

import Login from '@/views/Login'
// import LoginFacebook from '@/views/LoginFacebook'
import Register from '@/views/Register'
import Home from '@/views/Home'
import ProfileUpdate from '@/views/ProfileUpdate'
import Settings from '@/views/Settings'
import ForgetPasswordRequest from '@/views/ForgetPasswordRequest'
import Wallet from '@/views/Wallet'
import WalletTopUp from '@/views/WalletTopUp'
import Inbox from '@/views/Inbox'
import Merchant from '@/views/Merchant'
import MerchantDeal from '@/views/MerchantDeal'
import ItemCustomisation from '@/views/ItemCustomisation'
import Cart from '@/views/Cart'
import OrderDetails from '@/views/OrderDetails'



Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      name: 'login',
      path: '/login',
      component: Login,
    },
    {
      name: 'register',
      path: '/register',
      component: Register,
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
    },
    {
      name: 'settings',
      path: '/settings',
      component: Settings,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'profile',
      path: '/profile',
      component: ProfileUpdate,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'merchant',
      path: '/merchants/:merchantId',
      component: Merchant,
      props: true,
    },
    {
      name: 'merchantDeals',
      path: '/merchants/:merchantId/deals',
      component: MerchantDeal,
      props: true,
    },
    {
      name: 'itemCustomisation',
      path: '/merchants/:merchantId/items/:itemId',
      component: ItemCustomisation,
      props: true,
    },
    {
      name: 'inbox',
      path: '/inbox',
      component: Inbox,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'wallet',
      path: '/wallet',
      component: Wallet,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'walletTopUp',
      path: '/wallet/topup',
      component: WalletTopUp,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'cart',
      path: '/cart',
      component: Cart,
      meta: {
        // requiresAuth: true
      }
    },
    {
      name: 'orderDetails',
      path: '/orders/:orderId',
      component: OrderDetails,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'forget_password',
      path: '/forget_password',
      component: ForgetPasswordRequest,
    },

    // {
    //   path: '/comics',
    //   name: 'Comics',
    //   component: Comics,
    //   meta: {
    //     requiresAuth: true
    //   }
    // },

    // otherwise redirect to home
    { 
      name: 'NotFound',
      path: '*', 
      redirect: '/' 
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})


router.beforeEach((to, from, next) => {

  let requiresAuth = to.matched.some(record => record.meta.requiresAuth)

  let isPublicOnly = [ 'login', 'register', 'forget_password' ].includes(to.name)

  // console.log(requiresAuth, isPublicOnly)
  
  store.commit(SWITCH_OVERLAY_LOADER, { root: true });

  let isLogin = store.dispatch(CHECK_AUTH);
  
  store.commit(SWITCH_OVERLAY_LOADER, { root: true });

  console.log('isLogin', isLogin, isLogin == true, isLogin == false);
  
  if (requiresAuth){
    //Auth page only

    isLogin.then(
      // If auth success

      values => {
        if(values == false){
          next('login')
        }
    });

    //TODO: What if CHECK AUTH throw error

  }else{
    // Auth not required

    if(isPublicOnly){
      // If auth kick out to home
      isLogin.then((values) => {
          if(values == false){
            // Not auth
            next()
          }else{
            // Auth kick out
            next('home')
          }
      })
      .catch((error) => {
        // Error in auth chechking, but public only
        alert("You are logged out")
        console.log(error);
        next('login')
      });
    }

    // Auth not required and not public only
    // Continue out of if
  }

  next()

});




export default router


