import Vue from 'vue'

// import "./registerServiceWorker";

const prod = process.env.NODE_ENV === 'production'
const shouldSW = 'serviceWorker' in navigator && prod
if (shouldSW) {
  navigator.serviceWorker.register('/service-worker.js').then(() => {
    console.log("Service Worker Registered!")
  })
}

console.log('window.navigator.standalone', window.navigator.standalone);


import VueElementLoading from 'vue-element-loading'
Vue.component('VueElementLoading', VueElementLoading)

import Toasted from 'vue-toasted';
Vue.use(Toasted)


import Vue2Filters from 'vue2-filters' 
Vue.use(Vue2Filters)

import VueAnalytics from 'vue-analytics'


import VueOffline from 'vue-offline'
Vue.use(VueOffline)


import VueContentPlaceholders from 'vue-content-placeholders'
Vue.use(VueContentPlaceholders)

// Vue.use(fab)

// import Vuetify from 'vuetify'

// import VueFire from 'vuefire'
// import firebase from 'firebase/app'
// import 'firebase/firestore'
// Vue.use(VueFire)

import App from './App'
import router from './router'

const isProd = process.env.NODE_ENV === 'production'

if(isProd){
  Vue.use(VueAnalytics, {
    id: 'UA-135031290-1',
    router,
    debug: {
      enabled: !isProd,
      sendHitTask: isProd
    }
  })
}

import ApiService from "./common/api.service";
import DateFilter from "./common/date.filter";
import ErrorFilter from "./common/error.filter";

// import GeoService from "./common/geo.service";

import store from './store'

// import { CHECK_AUTH } from "./store/actions.type";

Vue.config.productionTip = false

ApiService.init();

// console.log(GeoService.getCurrentCoordinates());


// Vue.use(Vuetify)


Vue.filter("date", DateFilter);
Vue.filter("error", ErrorFilter);
Vue.filter('capitalize', function (value) {
  if (!value) return ''
  value = value.toString()
  return value.charAt(0).toUpperCase() + value.slice(1)
})
Vue.filter('currency', function (value) {
  return 'SGD' + ' ' + parseFloat(value).toFixed(2);
});
Vue.filter('2decimal', function (value) {
  return '' + parseFloat(value).toFixed(2);
});
Vue.filter('floor', function (value) {
  return '' + Math.floor(parseFloat(value));
});
Vue.filter('newLiner', function (value) {
  return '' + value.replace(/\n/g, "<br>");
});




// firebase.initializeApp({
//   projectId: "vue-app-fb29a",
//   messagingSenderId: "972401469784"
// })
// export const db = firebase.firestore()


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



