<template>
  <div class="container">
    <div v-if="!hasOrder">
      <content-placeholders>
        <content-placeholders-heading :img="true" />
        <content-placeholders-text :lines="3" />
      </content-placeholders>
    </div>
    <div v-if="hasOrder">
      <div class="row">
        <div class="col-xs-12">
          <br>
          <h1>Order Details</h1>
        </div>
      </div>
      <hr>
      <router-link :to="{ name: 'merchant', params: { merchantId: currentOrderDetails.merchant.friendlyId } }">
        <div class="row">
          <div class="col-xs-3">
            <img :src="currentOrderDetails.merchant.image" class="img-fluid" />
          </div>
          <div class="col-xs-9">
            <h3><b>{{ currentOrderDetails.merchant.name }}</b></h3>
            <h5>{{ currentOrderDetails.merchant.address }}</h5>
          </div>
        </div>
      </router-link>
      <br>
      <div class="row">
        <div class="col-xs-12 text-xs-left">
          <table class="table">
            <tbody>
              <tr>
                <td rowspan="4">
                  <!-- <b>Order QR</b> -->
                  <img :src="orderQR" class="img-fluid" />
                </td>
                <td>
                  <b>Ref#</b>
                </td>
                <td>
                  <b>{{ orderIdSplit[0] }}</b><small> -{{ orderIdSplit[1] }}</small>
                </td>
              </tr>
              <tr>
                <td>
                  <b>STASHQ</b>
                </td>
                <td>
                  <small>Q- </small><b>{{ parseInt(currentOrderDetails.q_no) }}</b>
                </td>
              </tr>
              <tr>
                <td>
                  <b>Date</b>
                </td>
                <td>
                  {{ currentOrderDetails.orderDateTime }}
                </td>
              </tr>
              <!-- <tr>
                <td>
                  <b>Collection</b>
                </td>
                <td>
                  5 min pick-up
                </td>
              </tr> -->
              <tr>
                <td>
                  <b>Status</b>
                </td>
                <td>
                  {{ currentOrderDetails.latestStatus }}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- <hr> -->
      <div class="row">
        <div class="col-xs-12">
          <!-- <h4>Items</h4> -->
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">Item</th>
                  <th scope="col">Total</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, idx) in currentOrderDetails.items" :key="idx">
                  <td>
                    <b>{{ item.name }}</b><br>
                    <span v-html="item.additional.replace(/(\||,)/g, '-').replace('N/A', '').replace(/\n/g, '<br>')">
                    </span>
                  </td>
                  <td class="text-xs-right">SGD {{ item.value | 2decimal }}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td class="text-xs-right">Subtotal</td>
                  <td class="text-xs-right">SGD {{ calcSubTotal() | 2decimal }}</td>
                </tr>
                <!-- <tr>
                  <td class="text-xs-right">Promo description if any</td>
                  <td class="text-xs-right">SGD 20</td>
                </tr>
                <tr>
                  <td class="text-xs-right">Incl. GST</td>
                  <td class="text-xs-right">SGD 20</td>
                </tr> -->
                <tr class="font-weight-bold">
                  <td class="text-xs-right">Total</td>
                  <td class="text-xs-right">SGD {{ calcTotal() | 2decimal }}</td>
                </tr>
              </tfoot>
            </table>
            <br>
            <br>
        </div>
      </div>
      <!-- <div class="row" v-if="isReportOrderAvailable() && !isReportOrder">
        <div class="col-xs-12">
          <button type="button" class="btn btn-outline-danger btn-lg btn-block" @click="reportOrder">Report Order</button>
          <br>
        </div>
      </div> -->
      <!-- <div class="row" v-if="currentOrderDetails.latestStatus != 'Waiting' && !isCancelOrder">
        <div class="col-xs-12 text-xs-center">
          <br> -->
          <!-- <button type="button" class="btn btn-outline-danger btn-lg btn-block" @click="cancelOrder">Cancel Order</button> -->
          <!-- <span class="redFont" @click="cancelOrder">Cancel order</span>
        </div>
      </div> -->
    </div>
    <div v-if="isReportOrder && hasOrder">
      <div class="row">
        <div class="col-xs-12 text-xs-center">
          <h1>Order Report</h1>
          <hr>
        </div>
      </div>
    </div>
    <div v-if="isCancelOrder && hasOrder">
      <div class="row">
        <div class="col-xs-12 text-xs-center">
          <br>
          <h1>Cancel Order</h1>
          <hr>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import { FETCH_ORDER_DETAILS } from "@/store/actions.type";

import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

export default {
  name: "viewOrderDetails",
  data() {
    return {
      isReportOrder: false,
      isCancelOrder: false,
    };
  },

  mounted () {
    Promise.all([
      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true }), 
      this.$store.dispatch(FETCH_ORDER_DETAILS, this.$route.params.orderId), 
      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })
    ]);
  },

  computed: {
    ...mapGetters([ 'hasOrder', 'currentOrderDetails', 'orderQR', 'orderIdSplit', ]),
  },

  methods: {
  
    isReportOrderAvailable(){
      let result = false
      
      var date1 = new Date(this.currentOrderDetails.orderDateTime);
      var date2 = new Date();
      var timeDiff = Math.abs(date2.getTime() - date1.getTime());
      var diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24)); 
      
      console.log('order reporting feature', date1, date2, diffDays)

      return (diffDays < 14)
    },
  
    calcSubTotal(){
      let result = 0
      for(let i = 0; i < this.currentOrderDetails.items.length; i++){
        result += this.currentOrderDetails.items[i].value
      }
      return parseFloat(result);
    },
    
    calcTotal() {    
      return this.calcSubTotal();
    },

    cancelOrder () {
      if (window.confirm('Do you want to cancel the order?')) {
        // this.$store.commit(REMOVE_FROM_CART, idx)
        alert("Cancelling your order")
        this.isCancelOrder = !this.isCancelOrder;
      }else{
        return 0;
      }
    },
    
    reportOrder () {
      alert("Reporting your order")
      this.isReportOrder = !this.isReportOrder;
    }
  },

  // beforeRouteEnter(to, from, next) {
  //   // this.$store.dispatch(FETCH_ORDER_DETAILS, to.params.orderId);
  // }

};
</script>

<style scoped>
.redFont{
  color: red;
  font-size:larger;
}
</style>


