<template>
  <div class="">
    <div class="container" v-if="!isFacebookLogin">
      <div class="row">
        <div class="col-xs-12">
          <h1 class="text-xs-left">Sign in</h1>
          <p class="text-xs-left">
            <router-link :to="{ name: 'register' }">
              Need an account?
            </router-link>
          </p>
          <ul v-if="errors" class="error-messages">
            <li v-for="(v, k) in errors" :key="k">{{ k }} {{ v | error }}</li>
          </ul>
          <form v-on:submit.prevent="onSubmit(email, password);">
            <fieldset class="form-group">
              <input
                class="form-control form-control-lg"
                type="text"
                v-model="email"
                placeholder="Email"
              />
            </fieldset>
            <fieldset class="form-group">
              <input
                class="form-control form-control-lg"
                type="password"
                v-model="password"
                placeholder="Password"
              />
            </fieldset>
            <fieldset class="form-group">
              <router-link :to="{ name: 'forget_password', params: {} }">
                or click here to reset password
              </router-link>
            </fieldset>
          </form>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <button class="btn btn-lg btn-block btn-primary" @click="onSubmit()">
            Sign in
          </button>
          <br>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <button class="btn btn-lg btn-block btn-primary googleLoginButton" @click="switchFbLoginForm()">
            Google Login
          </button>
          <br>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <button class="btn btn-lg btn-block btn-primary fbLoginButton" @click="switchFbLoginForm()">
            Facebook Login
          </button>
        </div>
      </div>
    </div>
    <div class="container" v-if="isFacebookLogin">
      <div class="row">
        <div class="col-xs-12">
          <br>
          <br>
          <h1 class="text-xs-left">Facebook Login</h1>
          <hr>
          <p>
            Facebook login has been deactivated in our system. Below the steps to acquire your account:
          </p>
          <p>
            <ol class="fbLoginForgetPassword">
              <li>
                Click reset password below
              </li>
              <li>
                Insert your email address used for Facebook
              </li>
              <li>
                Click Submit
              </li>
              <li>
                A temporary password will be sent to your address.
              </li>
            </ol>
          </p>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-6">
          <br>
          <button class="btn btn-lg btn-primary" @click="switchFbLoginForm()">Back to login page</button>
        </div>
        <div class="col-xs-6">
          <br>
          <router-link :to="{ name: 'forget_password', params: {} }" class="btn btn-lg" >
            or reset password
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { LOGIN } from "@/store/actions.type";

import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

export default {
  name: "viewLogin",
  data() {
    return {
      email: null,
      password: null,
      isFacebookLogin: false,
      isGoogleLogin: false,
    };
  },
  methods: {
    onSubmit() {

      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });

      this.$store
        .dispatch(LOGIN, { email: this.email, password: this.password })
        .then(data => {
          console.log('dispatched login', data)
          this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })
          if(!data){
             alert('No account found, please sign up for an account')
             return
          }
          this.$router.push({ name: "home" });
        })
        // .catch(error => {
        //   alert('No account found, please sign up for an account')
        //   this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });
        // });

    },

    switchFbLoginForm() {
      this.isFacebookLogin = !this.isFacebookLogin;
    },

    switchGoogleLoginForm() {
      this.isGoogleLogin = !this.isGoogleLogin;
    }
  },
  
  computed: {
    ...mapState({
      errors: state => state.auth.errors
    })
  }
};
</script>

<style>
.fbLoginButton{
  background-color: #3b5998;
}
.googleLoginButton{
  background-color: #DB4437;
}
ol.fbLoginForgetPassword > li{
  margin-top: 15px;
}
</style>



