<template>
  <div class="home-page">
    <div class="container page" v-if="isLocationError">
      <div class="row">
        <div class="col-xs-12">
          <br>
          <content-placeholders>
            <content-placeholders-heading :img="true" />
            <content-placeholders-text :lines="3" />
          </content-placeholders>
        </div>
      </div>
    </div>
    <div class="container page" v-if="isLoadingContent">
      <div class="row">
        <div class="col-xs-12">
          <br>
          <h1>Location not found</h1>
          <p>
            Please turn on Location Services. For the best experience, please set it to High Accuracy
          </p>
          <hr>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <br>
          <button class="btn btn-lg btn-primary btn-block" @click="refreshContent">Refresh content</button>
        </div>
      </div>
    </div>
    <div class="container page" v-if="!isLocationError && !isLoadingContent">
      <br><h2><b>Food and Beverage</b></h2><br>
      <div class="row justify-content-center" v-if="merchants.length >= 1">
        <div v-for="(merchant, idx) in merchants" :key="idx"
            class="col-xs-6 col-md-3 itemGrid">
          <div
            class="merchantCard"
            @click="openMerchant(idx)"
          >
            <div class="merchantImage img-fluid">
              <div v-if="!merchant.currentlyOp" class="notOpOverlay">
                <div class="text">Closed</div>
              </div>
              <img class="img-fluid" :src="merchant.image" />
            </div>
            <small>
              <span v-for="(tag, idx2) in merchant.tags" :key="idx2" class="merchantTags">
                {{ tag }}
                <span v-if="idx2 != merchant.tags.length - 1"> - </span>
              </span>
              </small><br>
            <b>{{ merchant.name.toUpperCase() }}</b><br>
            <span>{{ merchant.address }}<small> ({{ merchant.distance | floor }} km)</small></span><br>
          </div>
        </div>
      </div>
      <div class="row justify-content-center" v-if="merchants.length <= 0">
        No merchant found around you, please try again
      </div>
    </div>
  </div>
</template>


<script>
import { mapGetters } from "vuex";
import { FETCH_MERCHANTS } from "@/store/actions.type";

import GeoService from "@/common/geo.service";

import { SET_CURRENT_LOCATION, NEW_CART } from "@/store/mutations.type";

export default {
  name: "viewHome",

  data() {
    return {
      isLoadingContent: true,
      isLocationError: true,
      locationCoord: {
        lat: 0,
        lon: 0,
      },
      error: '',
    }
  },

  computed: {
    // ...mapState({
    //   errors: state => state.auth.errors
    // }),
    ...mapGetters(["currentCart", "merchants"])
  },

  mounted() {
    this.refreshContent();
  },

  methods: {
    refreshContent() {
      this.isLoadingContent = true;
      this.isLocationError = false;
      this.geolocation()
    },

    geolocation() {
      navigator.geolocation.getCurrentPosition(this.buildUrl, this.geoError);
    },

    buildUrl(position) {
      this.locationCoord.lat = position.coords.latitude;
      this.locationCoord.lon = position.coords.longitude;

      this.$store.commit(SET_CURRENT_LOCATION, {
        lat: this.locationCoord.lat,
        lon: this.locationCoord.lon,
      }, { root: true });

      this.fetchMerchants(position.coords.latitude, position.coords.longitude);
      this.isLoadingContent = false;
    },

    geoError(error) {
      this.error = error.code

      this.isLoadingContent = false;
      this.isLocationError = true;
      
      switch(error.code) {
          case error.PERMISSION_DENIED:
              this.error =  "User denied the request for Geolocation."
              break;
          case error.POSITION_UNAVAILABLE:
              this.error =  "Location information is unavailable."
              break;
          case error.TIMEOUT:
              this.error =  "The request to get user location timed out."
              break;
          case error.UNKNOWN_ERROR:
              this.error =  "An unknown error occurred."
              break;
      }

      console.log(this.error)

      alert("Please turn on Location Services. For the best experience, please set it to High Accuracy")
    },

    getLocation() {
      return GeoService.getCurrentCoordinates().then(function(value) {
        // console.log('[STASH]LOCATION:', value.latitude, value.longitude);
        // this.fetchMerchants(value.latitude, value.longitude);
        // this.$store.dispatch(FETCH_MERCHANTS);
        return  { lat: value.latitude, long: value.longitude }
      }).catch(function(error) {
        // alert("Please turn on Location Services. For the best experience, please set it to High Accuracy")
        console.log(error);
        return null;
      });
    },

    fetchMerchants (lat, lng) {
      console.log('fetchMerchants', lat, lng)
      this.$store.dispatch(FETCH_MERCHANTS);
      this.isLoadingContent = false;
      this.isLocationError = false;
    },

    openMerchant (merchantIdx) {
      // console.log("currentCart", this.currentCart.merchant.friendlyId)
      let merchant = this.merchants[merchantIdx]

      console.log('merchant selected', merchant)

      // console.log("nextMerchant", merchant.friendlyId)

      if(this.currentCart.items.length >= 1){
        // Item in current cart exist

        if (this.currentCart.merchant.friendlyId != merchant.friendlyId){
          // Not same merchant

          // Ask user to abandon previous cart
          if (window.confirm(this.currentCart.merchant.name + ' cart is found, would you like to proceed to visit ' + merchant.name + '?')) {
            
            // If user agree to abandon the previous cart

            this.$store.commit(NEW_CART, merchant, { root: true });
            this.$router.push({ name: "merchant", params: { 'merchantId': merchant.friendlyId } })
            
          }else{
            
            // User doesnt agree to abandon previous cart

            return 0;
          }
        }else{
          // Same merchant

          //Just proceed dont create new cart
          this.$router.push({ name: "merchant", params: { 'merchantId': merchant.friendlyId } })

        }
      }else{
        // No item in cart

        this.$store.commit(NEW_CART, merchant, { root: true });
      }

      this.$router.push({ name: "merchant", params: { 'merchantId': merchant.friendlyId } })
    },
  }
};
</script>


<style scoped>
  .img-fluid{
    object-fit: cover;
    width:175px;
    height:175px;
  }

  .itemGrid{
    margin-bottom: 20px;
  }
  
  .merchantTags{
    color: #ffba00;
    padding-bottom: -50px;
  }

  .merchantCard {
    color: black;
  }

  .merchantImage{
    position: relative;
  }

  .notOpOverlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0.7;
    background-color: #777;
  }

  .notOpOverlay > .text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    font-weight: bold;
    color: black;
  }

</style>


