import OrdersService from "@/common/orders.service";

import { FETCH_ORDER_DETAILS } from "@/store/actions.type";

import { SET_CURRENT_ORDER_DETAILS } from "@/store/mutations.type";
import { resolve } from "path";

const state = { 
  currentOrderDetails: {

  }
};

const actions = {
  async [FETCH_ORDER_DETAILS](context, orderId) {
    const { data } = await OrdersService.get(orderId)
    data.order.orderId = orderId;
    context.commit(SET_CURRENT_ORDER_DETAILS, data.order);
  },
};

const getters = { 
  currentOrderDetails() {
    return state.currentOrderDetails;
  },

  orderIdSplit() {
    var result = state.currentOrderDetails.orderId.split("-");
    var result1 = result[0]
    result.shift();
    return [ result1, result.join("-") ];
  },

  orderQR() {
    let qrBase = "https://chart.googleapis.com/chart?cht=qr&chs=200x200&chld=H|1&choe=UTF-8&chl="
    return (qrBase+state.currentOrderDetails.orderId);
  },

  hasOrder() {
    return state.currentOrderDetails.hasOwnProperty('orderId')
  },
};

const mutations = { 
  [SET_CURRENT_ORDER_DETAILS](state, order) {
    state.currentOrderDetails = order;
    console.log("SET_CURRENT_ORDER_DETAILS", order)
  },
};

export default {
  state,
  actions,
  mutations,
  getters
};


