<template>
  <div class="container">
    <br>
    <div class="row">
      <div class="col-xs-12">
        <!-- <img :src="item.image" /> -->
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <router-link :to="{ name: 'merchant', params: { merchantId: this.merchant.friendlyId } }">
          <b><b class="ion-android-arrow-back mainColor"></b> {{ merchant.name }} / {{ merchant.address }}</b>
        </router-link>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-9">
        <h1>{{ item.name }}</h1>
      </div>
      <div class="col-xs-3 text-xs-right">
        <!-- <h4><b class="strikethrough"><small>SGD {{ item.price }}</small></b></h4> -->
        <h4><b class="mainColor">SGD {{ calcItemValue() | 2decimal }}</b></h4>
      </div>
    </div>
    <hr>
    <br>
    <div class="row" v-for="(custom, idx) in item.customisation" :key="idx">
      <div class="col-xs-12">
        <ItemCustomiseCategory :customObject='custom'></ItemCustomiseCategory>
        <hr v-if="idx != item.customisation.length - 1">
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <br>
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="addItem">Submit</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import store from "@/store";
import router from "@/router";

import { ADD_TO_CART } from "@/store/mutations.type";

import ItemCustomiseCategory from "@/components/ItemCustomiseCategory";
import MenuCategoryVue from '../components/MenuCategory.vue';

export default {
  name: "viewItemCustomise",
  data() {
    return {
    };
  },
  components: {
    ItemCustomiseCategory
  },
  computed: {
    ...mapGetters(["merchant", "tempItem"]),
    item () {
      // console.log(this.merchant.menu.find(menuCategory => menuCategory.menuList.find(item => item.id === this.$route.params.itemId)).menuList.find(item => item.id === this.$route.params.itemId))
      return this.merchant.menu.find(menuCategory => menuCategory.menuList.find(item => item.id === this.$route.params.itemId)).menuList.find(item => item.id === this.$route.params.itemId);
    }
  },
  methods: {
		calcItemValue(){

      let result = this.tempItem.price
      
      for(let i = 0; i < this.tempItem.customisationApplied.length; i++){
        result += this.tempItem.customisationApplied[i].price
      }

      return result;
    },
    
    addItem () {

      // console.log('currentSelectedItem', this.item)

      for(let i = 0; i < this.item.customisation.length; i++){
      
        // console.log('currentSelectedItemCategory', this.item.customisation[i])

        let minItem = this.item.customisation[i].minSelection
        let maxItem = this.item.customisation[i].maxSelection
        let categoryName = this.item.customisation[i].name

        let count = 0

        if(maxItem == 0){
          maxItem = 999
        }

        for(let x = 0; x < this.tempItem.customisationApplied.length; x++){
          if(this.tempItem.customisationApplied[x].customiserCategoryName === categoryName){
            count += 1
          }
        }
        
        console.log('customCategory', this.tempItem.customisation[i].name, 'min', minItem, 'max', maxItem, 'count', count)
        console.log('currentTempItem', this.tempItem)

        if(count < minItem){
          alert('You have to select at least ' + String(minItem) + ' ' + this.tempItem.name + ' ' + categoryName)
          return;
        }

        if(count > maxItem){
          alert('You have reached max selection for ' + this.tempItem.name + ' ' + categoryName + ' (' + String(maxItem) + ' items)')
          return;
        }

      }

      // console.log('currentSelectedItem2', this.item)
      
      let toast = this.$toasted.show('<b>' + this.item.name.toUpperCase() + ' </b>&nbsp;added to your cart', { 
        theme: "bubble", 
        position: "bottom-center", 
        duration : 2000,
        
        action : {
            text : 'Cart',
            onClick : (e, toastObject) => {
                toastObject.goAway(0);
                this.$router.push({ name: 'cart', params: { }})
            }
        },
      });
      
      this.$store.commit(ADD_TO_CART);

      this.$router.push({ name: 'merchant', params: { merchantId: this.merchant.friendlyId }})
    }
  },

  beforeRouteEnter(to, from, next) {
		// console.log("Checking merchant in memory")
    // console.log(store.state)
    
    // console.log("Checking tempItem", store.state.cart.currentCart.tempItem)
    console.log("Checking tempItem", store.state.cart.currentCart.tempItem.hasOwnProperty('id'))
    if("Checking tempItem", store.state.cart.currentCart.tempItem.hasOwnProperty('id')){
      console.log("Checking tempItem", 'is found')
      next();
    }else{
      console.log("Checking tempItem", 'not found')
      // router.push({ name: 'home', params: { }})
      router.go(-1)
    }

		// if(store.state.currentCart.merchant){
		// 	if(to.params.merchantId == store.state.merchant.merchant.friendlyId){
		// 		// console.log("Merchant same, data not retrieved")
		// 		// console.log(store.state.merchant.merchant)

		// 		store.commit(REMOVE_TEMP_ITEM);
				
		// 		next();
				
		// 		return;
		// 	}
    // }
    

  },
}
</script>


<style scoped>
.align-text-bottom{
  vertical-align: bottom !important;
}
.mainColor{
  color: #ffba00;
}
.strikethrough{
  text-decoration: line-through;
}
</style>


