<template>
  <div>
    <div class="row" @click="selectItemCustomiserItem()">
      <div class="col-xs-9">
        <h5 v-bind:class="{ active: isSelected }">
          <i class="ion-android-checkbox mainColor" v-if="isSelected"></i><i class="ion-android-checkbox-outline-blank mainColor" v-if="!isSelected"></i>&nbsp;&nbsp;&nbsp;{{ customiser.name }}
        </h5>
      </div>
      <div class="col-xs-3 text-xs-right" v-bind:class="{ active: isSelected }">
        <h5>
          <small v-if="customiser.price == 0" v-bind:class="{ active: isSelected }">Free</small>
          <small v-if="customiser.price > 0" v-bind:class="{ active: isSelected }">+{{ customiser.price | currency }}</small>
        </h5>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import { ADD_CUSTOM_TO_TEMP_ITEM, REMOVE_CUSTOM_FROM_TEMP_ITEM } from "@/store/mutations.type";

export default {
  name: "ItemCustomiseCategory",
  props: {
    customiser: {
      type: Object,
      required: true
    },
    customiserCategory: {
      type: Object,
      required: true
    },
  },
  data () {
    return {
      isSelected: false,
    }
  },
  computed: {
    ...mapGetters([ 'tempItem', ])
  },
  methods: {
    selectItemCustomiserItem () {
      
      if(this.isSelected){
        this.$store.commit(REMOVE_CUSTOM_FROM_TEMP_ITEM, this.customiser)
        console.log("Item removed")
        this.isSelected = !this.isSelected
        return;
      }

      let _customiser = this.customiser;
      _customiser.customiserCategoryName = this.customiserCategory.name;

      let countCustomItemWithSameCategory = 0;
      
      for (let i = 0; i < this.tempItem.customisationApplied.length; i++) { 
        if(this.tempItem.customisationApplied[i].customiserCategoryName === this.customiserCategory.name){
          countCustomItemWithSameCategory += 1
        }
      }
      let max = this.customiserCategory.maxSelection;

      if(max == 0){
        max = countCustomItemWithSameCategory + 2
      }
      
      if(countCustomItemWithSameCategory < max){
        this.$store.commit(ADD_CUSTOM_TO_TEMP_ITEM, _customiser)
      }else{
        alert('You have reached max selection for ' + this.tempItem.name + ' ' + this.customiserCategory.name + ' (' + String(max) + ' items)')
        return;
      }
      
      this.isSelected = !this.isSelected
      
    }
  }
};
</script>

<style scoped>
.mainColor{
  color: #ffba00;
}
.active{
  font-weight: bold;
}
</style>


