<template>
  <div>
    <h4>{{ customObject.name }} 
      <small>
        (
          <span v-if="customObject.minSelection == customObject.maxSelection && customObject.minSelection >= 1">
            <b>required</b>
            <small>{{ customObject.minSelection }} {{ customObject.maxSelection | pluralize('selection') }}</small>
          </span>
          <span v-else>
            <span v-if="customObject.minSelection >= 1"> 
              <b>required</b> 
              <small>min. {{ customObject.minSelection }} {{ customObject.minSelection | pluralize('selection') }}</small>
            </span>
            <span v-if="customObject.minSelection == 0"> optional </span>
            <span v-if="customObject.maxSelection >= 1"><small>up to {{ customObject.maxSelection }} {{ customObject.maxSelection | pluralize('selection') }}</small></span>
          </span>
        )
      </small>
    </h4>
    <div>
      <div class="modifierItem" v-for="(item, idx) in customObject.items" :key="idx">
        <ItemCustomiseItem :customiser="item" :customiserCategory="customObject" ></ItemCustomiseItem>
        <hr v-if="idx != customObject.items.length - 1">
      </div>
    </div>
  </div>
</template>

<script>
import ItemCustomiseItem from '@/components/ItemCustomiseItem';

export default {
  name: "ItemCustomiseCategory",
  props: {
    customObject: {
      type: Object,
      required: true
    },
  },
  data () {
    return {

    }
  },
  components: {
    ItemCustomiseItem
  }
};
</script>

<style scoped>
.modifierItem{
  margin-top: 18px;
  padding-left: 15px;
}
</style>


