import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import JwtService from "@/common/jwt.service";
import { API_URL } from "@/common/config";

const ApiService = {
  init() {
    Vue.use(VueAxios, axios);
    Vue.axios.defaults.baseURL = API_URL;

    Vue.axios.defaults.headers.common["Cache-Control"] = 'no-cache';
    Vue.axios.defaults.headers.common["STASH-API-V2"] = 1;
    
  },

  setHeader(setAuthorization = true) {
    if(setAuthorization){
      Vue.axios.defaults.headers.common["Authorization"] = `Token ${JwtService.getToken()}`;
    }else{
      Vue.axios.defaults.headers.common["Authorization"] = '';
    }
  },

  query(resource, params) {
    return Vue.axios.get(resource, params)
    .catch((error) => {

      if(error == "Error: Network Error" || error.response.status == 500){
        alert("Looks like we weren't able to connect to our server. Please try again in a few minutes.")
      }

      if(error.response.status == 401){
        alert("Please login to continue")
      }
      
      if (error.response) {
        console.log('error response', error.response.status);
      } else if (error.request) { 
        console.log('error Request', error.request);
      } else { 
        console.log('error Error', error.message);
      }

      throw error

    });
  },

  get(resource, slug = "") {
    console.log(`${resource}/${slug}`)
    return Vue.axios.get(resource, { })
    .catch((error) => {

      if(error == "Error: Network Error" || error.response.status == 500){
        alert("Looks like we weren't able to connect to our server. Please try again in a few minutes.")
      }

      if(error.response.status == 401){
        alert("Please login to continue")
      }
      
      if (error.response) {
        console.log('error response', error.response.status);
      } else if (error.request) { 
        console.log('error Request', error.request);
      } else { 
        console.log('error Error', error.message);
      }

      throw error
    });
  },

  post(resource, params) {
    // console.log(params.params)
    return Vue.axios.post(`${resource}`, params)
    .catch((error) => {

      if(error == "Error: Network Error" || error.response.status == 500){
        alert("Looks like we weren't able to connect to our server. Please try again in a few minutes.")
      }

      if(error.response.status == 401){
        alert("Please login to continue")
      }

      if (error.response) {
        console.log('error response', error.response.status);
      } else if (error.request) { 
        console.log('error Request', error.request);
      } else { 
        console.log('error Error', error.message);
      }

      throw error
    });
  },

  update(resource, slug, params) {
    return Vue.axios.put(`${resource}/${slug}`, params)
    .catch((error) => {

      if(error == "Error: Network Error" || error.response.status == 500){
        alert("Looks like we weren't able to connect to our server. Please try again in a few minutes.")
      }

      if(error.response.status == 401){
        alert("Please login to continue")
      }
      
      if (error.response) {
        console.log('error response', error.response.status);
      } else if (error.request) { 
        console.log('error Request', error.request);
      } else { 
        console.log('error Error', error.message);
      }

      throw error
    });
  },

  put(resource, params) {
    return Vue.axios.put(`${resource}`, params)
    .catch((error) => {

      if(error == "Error: Network Error" || error.response.status == 500){
        alert("Looks like we weren't able to connect to our server. Please try again in a few minutes.")
      }

      if(error.response.status == 401){
        alert("Please login to continue")
      }
      
      if (error.response) {
        console.log('error response', error.response.status);
      } else if (error.request) { 
        console.log('error Request', error.request);
      } else { 
        console.log('error Error', error.message);
      }

      throw error
    });
  },

  delete(resource) {
    return Vue.axios.delete(resource)
    .catch((error) => {

      if(error == "Error: Network Error" || error.response.status == 500){
        alert("Looks like we weren't able to connect to our server. Please try again in a few minutes.")
      }

      if(error.response.status == 401){
        alert("Please login to continue")
      }
      
      if (error.response) {
        console.log('error response', error.response.status);
      } else if (error.request) { 
        console.log('error Request', error.request);
      } else { 
        console.log('error Error', error.message);
      }

      throw error
    });
  }
};

export default ApiService;


