<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
				<br>
				<h1>Reset your password</h1>
				<hr>
			</div>
		</div>
		<div class="row" v-if="!isPasswordEmailSent">
			<div class="col-xs-12">
				<p>
					Please enter your email address to search for your account.
				</p>
				<form>
					<fieldset>	
						<fieldset class="form-group">
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="email"
							placeholder="Email"
							/>
						</fieldset>
					</fieldset>
				</form>
				<br>
			</div>
		</div>
		<div class="row" v-if="!isPasswordEmailSent">
			<div class="col-xs-6">
				<router-link :to="{ name: 'login', params: {} }" class="btn btn-lg btn-outline-primary btn-block" >
					Cancel
				</router-link>
			</div>
			<div class="col-xs-6">
				<button class="btn btn-lg btn-primary btn-block" @click="forgetPasswordSendRequest()">
					Send reset email
				</button>	
			</div>
		</div>
		<div class="row" v-if="isPasswordEmailSent">
			<div class="col-xs-12">
				<p>
					Reset process has been started, please view your email
				</p>
			</div>
		</div>
		<div class="row" v-if="isPasswordEmailSent">
			<div class="col-xs-12">
				<router-link :to="{ name: 'login', params: {} }" class="btn btn-lg btn-outline-primary btn-block" >
					Return to login page
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import { FORGET_PASSWORD_REQUEST } from "@/store/actions.type";

	export default {
		name: "viewForgetPasswordRequest",
		data () {
			return {
				email: '',
				isPasswordEmailSent: false
			}
		},

		methods: {
			forgetPasswordSendRequest () {
      	this.$store.dispatch(FORGET_PASSWORD_REQUEST, this.email).then(response => {
					this.isPasswordEmailSent = true
				}).catch(error => {
					alert('Unable to reach server, please try again in a few minutes')
				});
			}
		}
	};
</script>



