
import { SET_ON_OVERLAY_LOADER, SET_OFF_OVERLAY_LOADER, SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

const state = { 
  overlayLoader: false
};

const actions = { };

const getters = { };

const mutations = { 
  [SET_ON_OVERLAY_LOADER](state) {
    state.overlayLoader = true;
  },

  [SET_OFF_OVERLAY_LOADER](state) {
    state.overlayLoader = false;
  },

  [SWITCH_OVERLAY_LOADER](state) {
    console.log("Switching overlay current state", state.overlayLoader)
    state.overlayLoader = !state.overlayLoader;
    console.log("Switching overlay updated state", state.overlayLoader)
  },
};

export default {
  state,
  actions,
  mutations,
  getters
};


