import { MerchantsService } from "@/common/merchants.service";

import { FETCH_MERCHANT } from "@/store/actions.type";

import { 
  SET_MERCHANT,
  SET_PERSISTENT_EXPANDED_MENU_CATEGORY 
} from "@/store/mutations.type";

const initialState = { merchant: null, };
export const state = { ...initialState };


export const actions = {
   async [FETCH_MERCHANT](context, merchantFriendlyId) {
    const { data } = await MerchantsService.get(merchantFriendlyId);
    // console.log(MerchantsService.get(merchantFriendlyId))
    context.commit(SET_MERCHANT, data.merchant);
    return data;
  },
};


export const mutations = {
  [SET_MERCHANT](state, merchant) {

    let _merchant = merchant;
    
    // console.log(_merchant)
    
    for (var i = 0; i < _merchant.menu.length; i++) {
      for (var x = 0; x < _merchant.menu[i].menuList.length; x++) {
        _merchant.menu[i].isSelected = false
        // for (var y = 0; y < _merchant.menu[i].menuList[x].customisation.length; y++) {
        //   for (var z = 0; z < _merchant.menu[i].menuList[x].customisation[y].items.length; z++) {
        //     _merchant.menu[i].menuList[x].customisation[y].items[z].isSelected = false
        //   }
        // }
      }
    }

    // console.log(_merchant)
    
    state.merchant = _merchant;
  },

  [SET_PERSISTENT_EXPANDED_MENU_CATEGORY](state, menuCategoryName) {

    // let _merchant = merchant;
    // state.merchant = merchant;

    // console.log(state.merchant)

    for (var i = 0; i < state.merchant.menu.length; i++) {
      for (var x = 0; x < state.merchant.menu[i].menuList.length; x++) {
        if(state.merchant.menu[i].name == menuCategoryName){
          // console.log("found",state.merchant.menu[i].isSelected)
          state.merchant.menu[i].isSelected = !state.merchant.menu[i].isSelected 
          // console.log("found2", state.merchant.menu[i].isSelected)
          return
        }
      }
    }
    
    // state.merchant.menuList[menuCategoryIdx].isSelected = true
    
  },
};


const getters = {
  merchant(state) {
    return state.merchant;
  },
};


export default {
  state,
  actions,
  mutations,
  getters
};



