import ApiService from "@/common/api.service";

const OrdersService = {
  query(type, params) {
    console.log('params', params)
    return ApiService.query("orders", {
      params: params
    });
  },

  create(params) {
    return ApiService.post("orders/create", params );
  },

  get(id) {
    return ApiService.query("orders", {
      params: {
        orderId: id
      }
    });
  },
  
  update(id, params) {
    return ApiService.update("orders", id, { 
      order: params 
    });
  },
};

export default OrdersService;


