<template>
	<div>
    <div v-on:click="toggleCategory()" class="menuCategory">
      <h4 class="text-uppercase"><i v-if="menuCategory.isSelected" class="ion-arrow-down-b"></i><i v-if="!menuCategory.isSelected" class="ion-arrow-right-b"></i>&nbsp;&nbsp;{{ menuCategory.name }}</h4>
    </div>
    <div class="menuList" v-if="menuCategory.isSelected" transition="expand">
      <div class="item" v-for="(item, idx) in menuCategory.menuList" :key="idx" @click="checkItem(idx)">
        <div class="row itemContent">
          <div class="col-xs-3">
            <img class="img-fluid" :src="item.image" />
          </div>
          <div class="col-xs-6 text-uppercase">
            <div class="font-weight-bold">{{ item.name }}</div>
          </div>
          <div class="col-xs-3">
            {{ item.price | currency }}
          </div>
        </div>
        <hr v-if="idx != menuCategory.menuList.length - 1">
      </div>
    </div>
	</div>
</template>


<script>
import { mapGetters } from "vuex";

import { ADD_TO_CART, SET_PERSISTENT_EXPANDED_MENU_CATEGORY, SET_TEMP_ITEM } from "@/store/mutations.type";

export default {
  name: "MenuCategory",
  props: {
    menuCategory: {
      type: Object,
      required: true
    },
  },
  data () {
    return {
      
    }
  },
  computed: {
    ...mapGetters(["currentCart"])
	},
  methods: {
    toggleCategory () {
      
      this.$store.commit(SET_PERSISTENT_EXPANDED_MENU_CATEGORY, this.menuCategory.name);
    },

    checkItem (itemId) {
      
      if(!this.$store.state.merchant.merchant.currentlyOp){
        
        let toast = this.$toasted.show(this.$store.state.merchant.merchant.name + ' @ ' + this.$store.state.merchant.merchant.address + ' is closed now', { 
          theme: "outline", 
          position: "bottom-center", 
          duration : 2000,
        });

        return
      }
      
      let item = this.menuCategory.menuList[itemId];
      item.customisationApplied = [ ]
      
      if(item.customisation.length >= 1){
        // Has customisation
      
        this.$store.commit(SET_TEMP_ITEM, item);

        this.$router.push({ name: 'itemCustomisation', params: { merchantId: this.currentCart.merchant.friendlyId, itemId: item.id }})
        
      }else{
        // Doesnt have customisation

        let toast = this.$toasted.show('<b>' + item.name.toUpperCase() + ' </b>&nbsp;added to your cart', { 
          theme: "bubble", 
          position: "bottom-center", 
          duration : 2000,
          
          action : {
              text : 'Cart',
              onClick : (e, toastObject) => {
                  toastObject.goAway(0);
                  this.$router.push({ name: 'cart', params: { }})
              }
          },
        });
      
        this.$store.commit(ADD_TO_CART, item);
      }

    }
  }
};
</script>

<style scoped>
  .menuCategory{
    padding: 12px 0;
  }
  .menuList{
    margin-top: -10px;
    padding-top: 12px;
    background-color: #f9f9f9;
  }
  .item{
    padding: 10px;
  }
  .itemContent{
    margin-top: -10px;
    padding-bottom: 12px;
  }
  hr{
    margin: 0;
  }
</style>


