<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>Checkout</h1>
      </div>
    </div>
    <hr>
    <router-link :to="{ name: 'merchant', params: { merchantId: merchant.friendlyId } }">
      <div class="row">
        <div class="col-xs-3">
          <img class="img-fluid" :src="merchant.image" />
        </div>
        <div class="col-xs-9">
          <h3><b>{{ merchant.name }}</b></h3>
          <h5 class="blackColor">{{ merchant.address }}</h5>
        </div>
      </div>
    </router-link>
    <hr>
    <div class="row">
      <div class="col-xs-6">
        <b class="mainColor">
          Eat in / Take out
        </b>
        <select class="form-control highlightedForm" v-model="collectionMethod" required>
          <option value="-1">-- Select --</option>
          <option value="eatIn" v-if="merchant.availableCollectionMethod == 1 || merchant.availableCollectionMethod == 2">Eat in</option>
          <option value="takeOut" v-if="merchant.availableCollectionMethod == 0 || merchant.availableCollectionMethod == 2">Take out</option>
        </select>
      </div>
      <div class="col-xs-6" v-if="collectionMethod == 'takeOut'">
        <b class="mainColor">I am coming in ...</b>
        <select class="form-control highlightedSubForm" v-model="collectionTime" required>
          <option value="-1">-- X minutes --</option>
          <option value="0">Now</option>
          <option value="5">5 minutes</option>
          <option value="10">10 minutes</option>
          <option value="15">15 minutes</option>
          <option value="30">30 minutes</option>
        </select>
      </div>
      <div class="col-xs-6" v-if="collectionMethod == 'eatIn'">
        <b class="mainColor">Table number</b>
        <input type="text" class="form-control highlightedSubForm" v-model="tableNumber" placeholder="#" required/>
      </div>
    </div>
    <hr>
    <div class="row">
      <div class="col-xs-12">
        <h4>Items</h4>
          <table class="table table-sm">
            <thead>
              <tr>
                <th scope="col-6">Item</th>
                <th scope="col=4">Add on</th>
                <th scope="col-2">Total</th>
              </tr>
            </thead>
            <tbody v-if="currentCart.items.length >= 1">
              <tr v-for="(item, idx) in currentCart.items" :key="idx" class="cartItem cellPadded">
                <td><b @click="removeItem(idx)"><i class="ion-android-cancel text-warning"></i></b>&nbsp;&nbsp; <b>{{ item.name }}</b></td>
                <td>
                  <span v-if="item.customisationApplied.length > 0">
                    <span><small><small>+Base {{ item.name }} (SGD {{ item.price | 2decimal }})</small></small></span>
                    <br>
                    <span v-for="(customisationItem, idx2) in item.customisationApplied" :key="idx2">
                      <small><small>
                        +{{ customisationItem.customiserCategoryName }} {{ customisationItem.name }} (<span v-if="customisationItem.price == 0">Free</span><span v-if="customisationItem.price > 0">{{ customisationItem.price | currency }}</span>)
                      </small></small>
                      <br>
                    </span>
                  </span>
                </td>
                <td class="text-xs-right">SGD {{ calcItemValue(item) | 2decimal }}</td>
              </tr>
            </tbody>
            <tbody v-else>
              <tr>
                <td colspan="3">
                  No item selected
                </td>
              </tr>
            </tbody>
            <tfoot v-if="currentCart.items.length >= 1">
              <tr class="cellPadded">
                <td colspan="2" class="text-xs-right">Subtotal</td>
                <td class="text-xs-right">SGD {{ calcSubTotal() | 2decimal }}</td>
              </tr>
              <!-- <tr v-if="!validPromo" class="cellPadded">
                <td colspan="2" class="text-xs-right">Discount</td>
                <td class="text-xs-right">SGD 0</td>
              </tr> -->
              <!-- <tr class="cellPadded">
                <td colspan="2" class="text-xs-right mainColor">
                  <b>
                    <router-link :to="{ name: 'merchantDeals', params: { merchantId: this.merchant.friendlyId } }">
                      <b>Select promotion</b>
                    </router-link>
                  </b>
                </td>
                <td class="text-xs-right">SGD 20</td>
              </tr> -->
              <!-- <tr class="cellPadded">
                <td colspan="2" class="text-xs-right">Incl. GST</td>
                <td class="text-xs-right">SGD 20</td>
              </tr> -->
              <tr class="font-weight-bold cellPadded">
                <td colspan="2" class="text-xs-right">Total</td>
                <td class="text-xs-right">SGD {{ calcTotal() | 2decimal }}</td>
              </tr>
            </tfoot>
          </table>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <input class="form-control" type="text" placeholder="Notes to merchant"
          v-model="this.currentCart.notes"
        />
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-6">
        <button type="button" class="btn btn-outline-primary btn-lg btn-block" @click="submitOrder('CASH')">
          Pay with CASH
        </button>
      </div>
      <div class="col-xs-6">
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="submitOrder('STASH')">
          Pay with STASH
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { REMOVE_FROM_CART, RESET_CART } from "@/store/mutations.type";
import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

import OrdersService from '@/common/orders.service';

export default {
  name: "viewCart",
  data() {
    return {
      collectionMethod: '-1',
      collectionTime: '-1',
      tableNumber: '',
      promoCode: '',
      isPromoLoading: false,
      validPromo: false,
      // validPromo: {
      //   promoId: '',
      //   promoName: '',
      //   conditions: [
          
      //   ],
      //   value: {
      //     type: 'BOGO(Cheaper) / value / percentage',
      //     effect: 'cart / product',
      //     amount: '',
      //   },
      // },
    };
  },
  computed: {
    ...mapGetters(["currentCart", "merchant"])
  },
  methods: {
    gotoPromoPage () {
      alert("No promo found")
    },

		calcItemValue(item){

      let result = parseFloat(item.price)

      // console.log(item)
      
      for(let i = 0; i < item.customisationApplied.length; i++){
        result += parseFloat(item.customisationApplied[i].price)
      }

      return result;
		},

		calcSubTotal(){
      let result = 0

      console.log('currentCart', this.currentCart, this.currentCart.items.length, 'items')
      
      console.log(result, 0)

      for(let i = 0; i < this.currentCart.items.length; i++){
        console.log(result, this.calcItemValue(this.currentCart.items[i]))
        result += parseFloat(this.calcItemValue(this.currentCart.items[i]))
      }
      
      console.log(result, 0)

      return result;
    },
    
    calcTotal() {
      return this.calcSubTotal();
    },

    removeItem(idx) {
      let item = this.currentCart.items[idx]
      
      if (window.confirm('Proceed removing ' + item.name + ' from your cart?')) {
        this.$store.commit(REMOVE_FROM_CART, idx)
      }else{
        return 0;
      }

      if(this.currentCart.items.length <= 0){
        this.$router.push({ name: 'merchant', params: { merchantId: this.currentCart.merchant.id }})
      }
    },

    calcTotalItemPrice (item) {
      return 0;
    },

    calcTotalOrder () {

    },

    calcSubTotalOrder () {

    },

    submitOrder (paymentType) {
      if(paymentType !== 'CASH' && paymentType !== 'STASH'){
        alert('Invalid payment method')
        return
      }

      if(this.currentCart.items.length == 0){
        alert('No item selected')
        return
      }
      if(this.collectionMethod == 'eatIn'){
        if(this.tableNumber.length < 1){
          alert('Please provide valid table number')
          return
        }else{
          // Proceed
        }
      }else if(this.collectionMethod == 'takeOut'){
        if([0, 5, 10, 15, 30].includes(parseInt(this.collectionTime))){
          // Proceed
        }else{
          alert('Please select pick-up time')
          return
        }
      }else{
        alert('Please select collection method')
        return
      }

      // alert('Processing order...')

      // Create new object for order submission

      let oInfo = {
        menu: [
          // {
          //   uniqueId: '',
          //   menuId: '',
          //   qty: '',
          //   menuPrice: '',
          //   variation: '',
          //   totalVariationPrice: '',
          //   totalPrice: '',

          //    name: '',
          // },
        ],
        promotion: { },
        itemCount: this.currentCart.items.length,
        subtotal: parseFloat(this.calcSubTotal()).toFixed(2),
        discount: '',
        tax: '',
        total: parseFloat(this.calcTotal()).toFixed(2),
      }
      
      oInfo.promotion = {
        promoId: '',
        promoCode: '',
        promoName: '',
      }
      
      oInfo.discount = 0
      
      oInfo.tax = 0

      for(let i = 0; i < this.currentCart.items.length; i++){
        let tmpVariation = ''
        let tmpVariationPrice = 0

        console.log('tmp', this.currentCart.items[i].customisationApplied)

        for(let x = 0; x < this.currentCart.items[i].customisationApplied.length; x++){
          tmpVariation = tmpVariation + this.currentCart.items[i].customisationApplied[x].customiserCategoryName + '|'
          tmpVariation = tmpVariation + this.currentCart.items[i].customisationApplied[x].name + '|'
          tmpVariation = tmpVariation + this.currentCart.items[i].customisationApplied[x].price + '\n'
          tmpVariationPrice = tmpVariationPrice + this.currentCart.items[i].customisationApplied[x].price
        }

        let tmpItem = {
          uniqueId: 0,
          menuId: this.currentCart.items[i].id,
          name: this.currentCart.items[i].name,
          qty: 1,
          menuPrice: this.currentCart.items[i].price,
          variation: tmpVariation,
          totalVariationPrice: tmpVariationPrice,
          totalPrice: this.currentCart.items[i].price + tmpVariationPrice,
        }

        oInfo.menu.push( tmpItem )
      }



      // console.log(this.currentCart)

      let orderObject = {
        merchant_id: this.currentCart.merchant.id,
        payment_type: paymentType,
        table_no: this.tableNumber,
        delay_by: this.collectionTime,
        amount: parseFloat(this.calcTotal()).toFixed(2),
        oinfo: JSON.stringify(oInfo),
      }


      console.log(orderObject)

      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });

      OrdersService.create(orderObject).then(data => {
        console.log('Order success', data.data.sales_id)
        
        this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });

        this.$store.commit(RESET_CART, { root: true });

        this.$router.push({ name: "orderDetails", params: { 'orderId': data.data.sales_id } })
      }).catch(error => {
        
        this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });
        
        console.log('Order not created', error, error.response)

        if(error.response.status != 400){
          alert('Unable to create order, please try again in a few minutes')
          return
        }
        
        if(error.response.data.status == 39){
          alert('Insufficient balance, please topup your wallet')
          return
        }else{
          alert('Unable to create order, please try again in a few minutes')
        }

      })


    }
  }
};
</script>

<style scoped>
.cartItem{
  font-size: 125%
}
tr.cellPadded > td  {
  padding: 15px 0; 
}
.mainColor{
  color: #ffba00;
}
.highlightedForm{
  border: 1px solid #ffba00;
  font-weight: bold;
  color: #2f3155;
  background-color: #ffba00;
}
.highlightedSubForm{
  border: 1px solid #ffba00;
  font-weight: bold;
  color: #2f3155;
}
.blackColor{
  color: #2f3155;
}
</style>


