<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
        <router-link :to="{ name: 'merchant', params: { merchantId: this.merchant.friendlyId } }">
          <b><b class="ion-android-arrow-back mainColor"></b> {{ merchant.name }} / {{ merchant.address }}</b>
        </router-link>
			</div>
		</div>
    <div class="row">
      <div class="col-xs-12">
        <h1>Deals</h1>
      </div>
    </div>
    <hr>
	</div>
</template>

<script>

import { mapGetters } from "vuex";

export default {
  name: "viewMerchantDeal",
  computed: {
    ...mapGetters(["merchant"]),
  },
  components: { },
  data() {
	  return { }
	},
}
</script>

<style scoped>

</style>


