export const MerchantsService = {
  query(type, params) {
    
    // console.log(ApiService.query("home/aroundme", {
    //   params: params
    // }));

    return ApiService.query("home/aroundme", {
      params: params
    });
	},
	
  get(id) {
    return ApiService.query("merchants", {
      params: {
        id: id
      }
    });
    
    return { data: { 
			merchant: {
				name: 'Thirsty Guys',
				address: 'Faculty of Science S16 NUS',
				image: 'https://via.placeholder.com/1200x500',
        isOpen: true,
        friendlyId: 'tgnus',
        currentDiscount: 0.05,
				menu: [
					{
            id: 1,
            name: 'Food',
            image: 'https://via.placeholder.com/1200x500',
						menuList: [
							{
								id: 1,
								name: 'Sunny side egg',
                price: 2,
                customisation: [
                ]
							}, {
								id: 2,
								name: 'Rice and Beef Brisket',
								price: 12.7,
                customisation: [
                ]
							}
						]
					},
				]
			}
		}}
  },
};


